<template>
  <div class="index">
    <!-- 搜索 -->
        <div class="search-box">
          <el-input v-model="pages.name" placeholder="请输入姓名" style="width:600px;" @input="getList()"></el-input>
          <button class="search-submit-btn"><i class="el-icon-search"></i>搜索</button>
        </div>

    <div class="container-bar">
      <table cellspacing="0" ceilpadding="0" class="table">
        <thead>
          <tr>
            <td width="200px">序号</td>
            <td width="200px">ID号</td>
            <td width="100px">姓名</td>
            <td width="60px">性别</td>
            <td width="200px">出生日期</td>
            <td width="100px">电话</td>
            <td width="60px">学历</td>
            <td width="150px">申请状态</td>
            <td width="400px">操作</td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(v, i) in lists" :key="i">
            <td>{{i+1 | FilterXuhao(pages.pageNo)}}</td>
            <td>{{v.identity_card}}</td>
            <td>{{v.name}}</td>
            <td>{{v.sex | FormatSex}}</td>
            <td>{{v.birthday | FormatBirth}}</td>
            <td>{{v.mobile_phone}}</td>
            <td>{{v.education | FormatEdu}}</td>
            <td><el-button :disabled="true" :type="v.status | FilterType" >{{v.status | FilterName}}</el-button></td>
            <td>
              <el-button type="primary" @click="showEdit(v)" :disabled="!v.status==0">编辑</el-button>
              <el-button type="success" @click="submitReview(v)" :disabled="!v.status==0">提交审核</el-button>
              <el-button type="danger" @click="delList(v)">删除</el-button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- 点击去往多少页 -->
    <div class="cur-to-pages" style="user-select:none;">
      当前第 {{pages.pageNo}} 页/共 {{datas.pages}} 页
      每页显示：
      <select v-model="pages.pageSize" @change="CurChange" style="height:40px;width:100px;">
        <option value="10">10</option>
        <option value="11">11</option>
        <option value="12">12</option>
      </select>
      <button @click="pageNoDel" :disabled="pages.pageNo==1 || lists.length==0" style="margin-left:50px;">&lt;</button>
      <span v-for="(v, i) in curToBtn" :key="i" :class="v==setclassNum?'active':''" @click="CurToPage(v)" >{{v}}</span>
      <button @click="pageNoAdd" :disabled="pages.pageNo==datas.pages || lists.length==0">&gt;</button>
    </div>

    <!-- 编辑 -->
    <div class="edit-cont-nav" v-if="showEditNav">
      <div class="edit-cont">
        <div class="edit-title">
          <span style="font-size:2em;">编辑</span>
          <hr color="#ecf0f1" />
        </div>
        <br />
        <div class="edit-cont-items" style="font-size:20px;">
          姓名： <input type="text" v-model="editObj.name" /><br />
          性别： <select v-model="editObj.sex">
                    <option value="man">男</option>
                    <option value="woman">女</option>
                </select><br />
          学历： <select v-model="editObj.education">
                    <option value="highschool">高中</option>
                    <option value="college">大学</option>
                </select><br />
          出生日期： <input type="date" v-model="editObj.birthday" /><br />
          电话号码： <input type="date" v-model="editObj.mobile_phone" /><br />
          <button id="submitEdit" @click="SubmitEdit">确认</button>
          <button id="closeEdit" @click="CloseEdit">关闭</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { LoanList, Edit, DeleteItem, submitToApprove } from "@/utils/api";

export default {
  data() {
    return {
      lists: [],
      pages: {
        pageNo: 1,
        pageSize: 12,
        name: ""
      },
      setclassNum: 1,
      datas: {},
      editObj: {},
      showEditNav: false
    };
  },
  methods: {
    getList() {
      LoanList(this.pages).then(res => {
        if (res.data.code == 20000) {
          this.datas = res.data.data.data; // 页数 条数 数据
          this.lists = res.data.data.data.data; // 数据
        }
      });
    },
    CurToPage(v) {
      if (v == "...") { return; }
      this.pages.pageNo = v;
      this.setclassNum = v;
      this.getList();
    },
    pageNoAdd() {
      this.pages.pageNo++;
      this.setclassNum++;
      this.getList();
    },
    pageNoDel() {
      this.pages.pageNo--;
      this.setclassNum--;
      this.getList();
    },
    CurChange() {
      this.getList();
    },
    showEdit(v) {
      this.editObj = v;
      if (this.showEditNav == false) { this.showEditNav = true;}
      else { this.showEditNav = false; }
    },
    CloseEdit() {
      if (this.showEditNav == false) { this.showEditNav = true; }
      else { this.showEditNav = false; }
    },
    SubmitEdit() {
      Edit(this.editObj).then(res => {
        if (res.data.code == 20000) {
          this.$notify({
            title: "恭喜您",
            message: "修改成功！",
            type: "success"
          });
          this.getList();
        }
      });
      if (this.showEditNav == false) { this.showEditNav = true;}
      else { this.showEditNav = false; }
    },
    delList(v) {
      // 删除list元素
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          DeleteItem(v).then(res => {
            if (res.data.code == 20000) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              this.getList();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    submitReview(v) {
      submitToApprove({ id: v.id }).then(res => {
        if (res.data.code == 20000) {
          this.$message({
            type: "success",
            message: "提交成功！!"
          });
          this.$router.push("/loanapp/prereview");
        }
      });
    }
  },
  filters: {
    FilterXuhao(data, num) {
      if (num == 1) { return data; }
      else { return num * 10 + data; }
    },
    FormatSex(data) {
      if (data == "man") { return "男"; }
      else if (data == "woman") { return "女"; } 
      else { return "出错"; }
    },
    FormatBirth(data) {
      let d = new Date(data);
      return ( d.getFullYear() + " 年 " + (d.getMonth() + 1) + " 月 " + d.getDate() + " 日" );
    },
    FormatEdu(data) {
      if (data == "college") { return "大学"; }
      else if (data == "highschool") { return "高中"; } 
      else { return "出错了"; }
    },
    FilterType(data) {
      switch (data) {
        case 0: return "info"; break;
        case 1: return "primary"; break;
        case 2: return "primary"; break;
        case 3: return "warning"; break;
        case 4: return "primary"; break;
        case 5: return "primary"; break;
        case 6: return "warning"; break;
        case 7: return "success"; break;
      }
    },
    FilterName(data) {
      switch (data) {
        case 0: return "进件状态"; break;
        case 1: return "提交初审"; break;
        case 2: return "初审通过"; break;
        case 3: return "初审拒绝"; break;
        case 4: return "提交终审"; break;
        case 5: return "终审通过"; break;
        case 6: return "终审拒绝"; break;
        case 7: return "生成合同"; break;
      }
    }
  },
  computed: {
    curToBtn() {
      if (this.lists.length == 0) { return; }
      else { let { pageNo } = this.pages;
        if (pageNo < 5) { return [1, 2, 3, 4, 5, "...", this.datas.pages]; }
        else if (pageNo >= this.datas.pages - 3) { return [ 1, "...",
            this.datas.pages - 4,
            this.datas.pages - 3,
            this.datas.pages - 2,
            this.datas.pages - 1,
            this.datas.pages ]; }
        else { return
            [ 1, "...", pageNo - 2, 
              pageNo - 1, 
              pageNo, 
              pageNo + 1, 
              pageNo + 2, "...", 
              this.datas.pages
            ];
            }
      }
    }
  },
  mounted() {
    this.getList();
  }
};
</script>
